.btnContainer{
    /* display: block; */
    height: 3rem;
}

.btn{
    width:100%;
    height: 100%;
    border-radius: 1.5rem;
    background-color: #FC7614;
    color: #ffffff;
    border-style: none;
    cursor: pointer;
}

.btn:active{
    background-color: #FFFFFF;
    color: #FC7614;
}